.app {
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 3rem;
}

.card-header {
  text-align: center;
  font-weight: bold;
  margin: 1rem;
}

.card model-viewer {
  background: #f3f3f3;
  width: 100%;
  height: 65%;
}

.card {
  display: flex;
  position: relative;
  flex-direction: column;
  height: 350px;
  width: 300px;
  padding: 1rem;

  border-radius: 16px;
  background: #f3f3f3;
  box-shadow: -1rem 0 3rem #dddddd;

  transition: 0.2s;
}

.card:hover {
  transform: translateY(-1rem);
}

.card:hover ~ .card {
  transform: translateX(130px);
}

.card:not(:first-child) {
  margin-left: -130px;
}

.mobile-card {
  margin-bottom: -130px;
}

.mobile-card:hover ~ .mobile-card {
  transform: translateY(130px);
}

.mobile-card:not(:first-child) {
  margin-left: 0px;
}

.ar-button {
  background-image: url('./assets/view-in-ar.png');
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: 12px 50%;
  background-color: #fff;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  bottom: 3%;
  padding: 0px 16px 0px 40px;
  font-family: Roboto Regular, Helvetica Neue, sans-serif;
  font-size: 14px;
  height: 36px;
  line-height: 36px;
  border-radius: 18px;
  border: 1px solid #dadce0;
}

.ar-button:active {
  background-color: #e8eaed;
}

.ar-button:focus {
  outline: none;
}

.ar-button:focus-visible {
  outline: 1px solid #4285f4;
}

.tech-header {
  text-align: center;
  font-weight: bold;
  margin: 0.5rem;
}

.tech-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card-tech {
  flex-direction: column;
  font-weight: bold;
  border-radius: 25px;
  border: 2px solid #60e8f7;
  padding: 0.2rem 0.5rem;
  margin: 0 0.5rem;
}

.card-tech:hover {
  border: 2px solid #313131;
}

.developer {
  text-align: center;
  font-weight: bold;
  color: black;
}

.developer a:link {
  color: black;
}

.developer a:visited {
  color: black;
}

.developer a:hover {
  color: orange;
}

.developer a:active {
  color: black;
}
